import React from "react";
import {Link} from "react-router-dom";
import "../scss/App.scss";

import { socials, projectsArr} from "../data/data"

const ProjectList = (props) => {

  const getPostlist = () => {
    let listItems = projectsArr.map((el, i) => 

    <li className="projItem" key={i}>
      <div className="innerItem">
        {/* <Link to={`/singleproject/${i}`}> */}
          <img className="responsiveImage" src={el.screenshot}></img>
        {/* </Link> */}
        {/* <Link to={`/singleproject/${i}`} style={{ textDecoration: 'none' }}> */}
          <h3>{el.title}</h3>
        {/* </Link> */}
        <h6>{el.tech}</h6>
        <p>{el.description}</p>
        <div className="demo-links">
          <span><a href={el.demo}>DEMO</a></span> <span>| </span>
          <span><a href={el.code}>CODE</a></span> 
        </div>
      </div>
    </li>)
    return listItems
  }

  return (
    <div>
      <ul className="projContainer">{getPostlist()}</ul>
    </div>
  )
}

export default ProjectList;